import Vue from 'vue'
import store from '../store';

export default function wxConfig({title = '', desc = '', link = '', imgUrl = ''} = {}) {
  title = title || `${store.state.userInfo.nickName||'DA'}~`; // 
  desc = desc || 'DA~~';// 
  link = link || location.href.split('#')[0];// JS
  imgUrl = imgUrl || 'http://iao55.top/novel/test/logo.jpg'; // 
  Vue.$http({
    method: 'get',
    url: '/novel/wechat/configSign',
    params: {
      url: location.href.split('#')[0]
    }
  }).then(res => {
    console.log(res);
    var data = res.data;
    if (data.status == 1) {
      console.log('config start');
      wx.config({
        debug: false, // ,apialertpclogpc
        appId: data.appId, // 
        timestamp: data.timestamp, // 
        nonceStr: data.noncestr, // 
        signature: data.signature,// 
        jsApiList: [
          'checkJsApi',
          'chooseWXPay',
          'onMenuShareTimeline',
          'onMenuShareAppMessage',
          'updateAppMessageShareData',
          'updateTimelineShareData',
          'onMenuShareQQ'
        ]// JS
      });
      wx.ready(function () {
        console.log('wxconfig success');
        /*js*/
        wx.checkJsApi({
          jsApiList: ['updateAppMessageShareData', 'updateTimelineShareData'], // JSJS2,
          success: function (res) {
            // apitruefalse
            // {"checkResult":{"chooseImage":true},"errMsg":"checkJsApi:ok"}
            let isAppMsg = res.checkResult.updateAppMessageShareData;
            let isTimeLine = res.checkResult.updateTimelineShareData;
            let ua=navigator.userAgent;
            let isAdr=ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1;
            if (isAppMsg&&!isAdr) {
              console.log('new share');
              wx.updateAppMessageShareData({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success:function(){
                  // console.log('');
                },
                complete: function (res) {
                  // console.log('');
                }
              });
              wx.updateTimelineShareData({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success:function(){
                  // console.log('');
                },
                complete: function (res) {
                  // console.log('');
                }
              });
            } else {
              console.log('old share');
              wx.onMenuShareAppMessage({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success:function(){
                  // console.log('share success');
                },
                complete: function (res) {
                  // console.log('share started');
                }
              });
              wx.onMenuShareTimeline({
                title: title,
                desc: desc,
                link: link,
                imgUrl: imgUrl,
                success:function(){
                  // console.log('share success');
                },
                complete: function (res) {
                  // console.log('share started');
                }
              });
            }
          }
        });
      });
      wx.error(function (res) {
        // console.log('wxconfig fail');
        // console.log(res);
        // configerrorconfigdebugresSPA
      });
    }
  }).catch(error => {
    console.log(error);
  });
}
Vue.prototype.$wxConfig = wxConfig;



